import React from 'react'
import '../App.css'
import './Style.css/WelcomePage.css'
import Links from './Links'
import Resume from './Resume'
import ProfilePic from '../Capture.PNG'

function WelcomePage() {
    
    return (
 
           
         <div class="flex flex-col  md:flex-row ">
      

                <div class="px-12 mt-20 mx-16">
                    <h1 class="text-4xl"><em>Hi All, I am Vineet Mishra
                        <span className="wave">👋</span></em>
                    </h1>
                    <br />
                    <h4 class="text-2xl text-gray-500 "><em>A passionate Full Stack Software Developer 🚀 <br /> having an experience of building Web
                    and<br /> Mobile applications with JavaScript/Reactjs<br />/Nodejs/SQL/NoSQL React Native and 
                         some other cool libraries and frameworks.</em></h4>

                    <br />
                    <Resume/>
                    <br />
                <Links/>

                </div>

                <div class="flex flex-col justify-center mx-32 mt-4  ">
                    <img className="Intro-img rounded-full" src={ProfilePic} alt="" />
                </div>
            </div>
    )
}

export default WelcomePage
